h1 {
  margin-bottom: 65px;
  font-weight: 400;
}

input::placeholder {
  color: #d7d7d7;
}

button {
  padding: 6px 15px;
}

.inactive-display p {
  color: #fff;
  width: 60%;
  margin-bottom: 25px;
  font-size: 1.5em;
}

.auth {
  width: 600px;
  height: 340px;
  margin: 100px auto;
  display: flex;
}

.login-box {
  width: 400px;
  position: relative;
}

.login-box svg {
  z-index: -5;
  filter: drop-shadow(0 0 3px #5c5c5c);
  position: absolute;
  top: 0;
}

.login-inter {
  padding: 30px;
}

.login-inputs input:first-of-type {
  margin-left: 60px;
}

.login-inputs input:last-of-type {
  margin-left: 30px;
}

.signup-box {
  width: 400px;
  margin-top: 30px;
  margin-left: -200px;
  position: relative;
}

.signup-box svg {
  z-index: -5;
  filter: drop-shadow(0 0 3px #5c5c5c);
  position: absolute;
  right: 0;
}

.signup-inter {
  text-align: right;
  padding: 30px;
}

.signup-box .inactive-display p {
  margin-left: 40%;
}

.signup-inputs input:first-of-type {
  margin-inline: auto 30px;
}

.signup-inputs input:nth-of-type(2) {
  margin-inline: auto 60px;
}

.signup-inputs input:nth-of-type(3) {
  margin-inline: auto 90px;
}

.password-info {
  margin-bottom: 10px;
  font-size: .8em;
  font-style: italic;
}
